	import ruiDatePicker from '../../components/rattenking-dtpicker/rattenking-dtpicker.vue'
	import tTable from '@/components/t-table/t-table.vue'
	import tTh from '@/components/t-table/t-th.vue'
	import tTr from '@/components/t-table/t-tr.vue'
	import tTd from '@/components/t-table/t-td.vue'
	
    var d = getNowFormatDate();
	
	function getNowFormatDate() {
		var array = new Array();
		var date = new Date();
		var seperator1 = "-";
		var seperator2 = ":";
		var month = date.getMonth() + 1;
		var strDate = date.getDate();
		if (month >= 1 && month <= 9) {
			month = "0" + month;
		}
		if (strDate >= 0 && strDate <= 9) {
			strDate = "0" + strDate;
		}
		var firstDayOfYear = date.getFullYear() + seperator1 + month + seperator1 + '01';
		var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate;      //当前时间  yyyy-MM-dd
		array.push(firstDayOfYear);
	    array.push(currentdate);
		return array;
	}
	
	export default {
		components: {
			ruiDatePicker,
			tTable,
			tTh,
			tTr,
			tTd
		},
		data() {
			return {
				size:'',
				data:[],
				max: 0,
				loadMoreText: "加载中...",
				showLoadMore: false,
				tableList: [],
				value1: d[0],    //开始时间
				value2: d[1],    //结束时间
				uid: '',
				title: '',
				curr_worktype_bh: '',
				curr_worktype_name: '',
				huohao: '',
				worktype:'',
				color:'',
				chuanghao:'',
				chima:'',
				userid:'',  //编号
				sl1: '',
				sl2: '',
				zs: '',
				dk: ''
			}
		},
		onBackPress(options) {
			if (options.from === 'backbutton') {
				uni.switchTab({
					url: '/pages/index/index'
				});
				return true;
			}
			return false;
		},
		onUnload() {
			this.max = 0,
			this.data = [],
			this.loadMoreText = "加载更多",
			this.showLoadMore = false;
			console.log('onUnload事件');
		},
		onReachBottom() {
			console.log("onReachBottom");
			console.log('max：'+this.max );
			if (this.max >= this.size) {
				this.loadMoreText = "没有更多数据了!"
				return;
			}
			this.showLoadMore = true;    //设置下拉提示的标记
			setTimeout(() => {
				this.setDate();
			}, 1000);
		}, 
		onLoad(r) {
			let temp = JSON.stringify(r);
			if(temp.indexOf('.html') < 0 && JSON.stringify(r) !='{}'){  
					this.huohao = r.huohao;
					this.chuanghao = r.chuanghao;
					this.color = r.color
					this.chima = r.chima
					this.userid = r.userid
					this.worktype = r.worktype
			}
			this.dk = this.websiteUrl
			uni.getStorage({
					key: 'fwqsj',
					success: res => {
						this.dk = res.data
					}
			}),
			uni.getStorage({
				key: 'loginData',
				success: res => {
					this.title = res.data.uname
					this.uid = res.data.uid
					this.curr_worktype_bh = res.data.curr_worktype_bh
					this.curr_worktype_name = res.data.curr_worktype_name
					this.initData();
				}
			})
		},
		methods: {
			initData(){
				setTimeout(() =>{
					uni.request({
						url: `https://${this.dk}/gongziMx.aspx`,
						method: 'GET',
						data: {
							uid:this.uid,
							dates:this.value1,
							datee:this.value2,
							huohao:this.huohao,
							worktype:this.worktype,
							color:this.color,
							chuanghao:this.chuanghao,
							chima:this.chima,
							userid:this.userid
						},
						success: res => {
						    this.tableList = res.data      //请求的所有数据
							this.size = this.tableList.length; 
							let temp = res.data;
							let sum = 0
							let sl1 = 0;
							for (let i = 0; i < temp.length; i++) {
								sum = sum + temp[i].je
								sl1 = sl1 + temp[i].sl
							}
							this.zs = sum.toFixed(2);
							this.sl1 = sl1
							this.value3 = ''
							this.value4 = ''
							this.sl2 = temp.length; 
							
							//一下为下拉使用
							
							this.max = 0;
							this.data = [];
							let data = [];
							this.max += (this.size<=20?this.size:20);
							let temp1 = this.tableList.splice(0,20);
							for(var i = 1;i <= temp1.length;i++){
								data.push(temp1[i-1]);
							}
							this.data = this.data.concat(data);
							uni.stopPullDownRefresh();				
						}
					})
				},2000);
			},
            setDate() {
            	let data = [];
            	this.max += (Math.floor(this.tableList.length/20)>0?20:this.tableList.length);
				//console.log(this.max);
				let temp1 = this.tableList.splice(0,20);
				for(var i = 1;i <= temp1.length;i++){
					data.push(temp1[i-1]);
				}
            	this.data = this.data.concat(data);
            },
			bindChange1(value) {
				this.value1 = value;
			},
			bindChange2(value) {
				this.value2 = value;
			},
			onpeDetailedMore(){
				uni.navigateTo({
					url:'../info/info1?flag=3'
				})
			},
			onpeDetailed() {
				/* uni.request({
					url: `https://${this.dk}/gongziMx.aspx`,
					method: 'GET',
					data: {
						uid: this.uid,
						dates: this.value1,
						datee: this.value2,
						huohao: this.huohao,
						worktype: this.worktype
					},
					success: res => {
						this.tableList = res.data
						let temp = res.data;
						let sum = 0
						let sl1 = 0;
						for (let i = 0; i < temp.length; i++) {
							//console.log(temp[i].je);
							sum = sum + temp[i].je
							sl1 = sl1 + temp[i].sl
						}
						this.zs = sum.toFixed(2);
						this.sl1 = sl1
						this.value3 = ''
						this.value4 = ''
						this.sl2 = temp.length
					}
				}); */
				this.initData();
			}
		},

	}
